@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-header{align-items:center;background-color:#1a237e;color:white;display:flex;flex-direction:row;height:128px;justify-content:space-between;margin-left:auto;margin-right:auto;top:0;width:100%}.header-left{padding:10px}.header-right{padding:10px}.product-card{align-content:flex-start;align-items:stretch;border-radius:5%;box-shadow:2px 4px 5px 0 grey;display:flex;flex-flow:column wrap;margin:10px}@media (max-width: 480px){.product-card{max-width:100%}}@media (max-width: 480px) and (orientation: landscape){.product-card{max-width:100%}}@media (min-width: 480px) and (orientation: portrait){.product-card{max-width:33.33%}}@media (min-width: 768px) and (orientation: landscape){.product-card{max-width:33.33%}}@media (min-width: 1024px) and (orientation: portrait){.product-card{max-width:25%}}@media (min-width: 768px) and (orientation: landscape){.product-card{max-width:25%}}.product-image-container{min-height:15em;position:relative}.product-image{background-position:50% 50%;background-repeat:no-repeat;background-size:cover;border-top-left-radius:5%;border-top-right-radius:5%;margin-left:auto;margin-right:auto;max-height:20em;min-height:15em;width:100%}.product-label{left:15px;position:absolute;top:15px}.product-rating{bottom:15px;left:15px;position:absolute}.product-favourite{bottom:15px;color:white;opacity:0;position:absolute;right:15px;-webkit-transition:.5s ease;transition:.5s ease}.product-image-container:hover .product-favourite{opacity:1}.product-favourite:hover{color:black}.badge{border-radius:7%;color:white}.badge-red{background-color:#df171e;padding:5px}.product-name{margin-bottom:auto;padding-left:10px;padding-right:10px}.product-info{display:flex;flex-direction:column;padding:10px}.product-style{align-items:center;display:flex}.product-origin{align-items:center;display:flex}.product-abv{align-items:center;display:flex}.style-icon{padding-right:1rem}.product-price{border-top:1px solid;display:flex;flex-direction:column;padding:10px}.product-form{align-items:stretch;display:flex;text-align:center}.oos-btn{text-align:center}.product-quantity{width:25%}.atb-btn{width:75%}html{box-sizing:border-box;font-family:"Roboto","normal"}@media screen and (max-width: 480px){.main-section{height:100%;overflow:auto;overflow-x:hidden;padding:1rem}}@media screen and (min-width: 1280px){.main-section{margin-left:auto;margin-right:auto;width:1280px}}.app-body{height:100%;width:100%}.loading-icon{left:50%;position:fixed;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.products{align-content:flex-start;align-items:stretch;display:flex;flex-flow:row wrap;justify-content:center}

